import axios from 'axios'
const baseUrl = '/api/books'

const searchBook = async (title) => {
    try{
        const request = await axios.get(`${baseUrl}/${title}`)
        return request.data
    } catch(err){
        return err
    }
    
  }

const searchTitle = async(bid) => {
    const request = await axios.get(`${baseUrl}/SearchTitle/${bid}`)
    return request.data
}

const searchAvail = async(bid) => {
    const request = await axios.get(`${baseUrl}/SearchAvailable/${bid}`)
    return request.data
}


export default { searchBook, searchTitle, searchAvail }