import {React, useState } from 'react';
import { Link, useHistory } from "react-router-dom"

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import { fade, makeStyles, useTheme } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import clsx from 'clsx';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

import storage from '../utils/storage'


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    color: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)'
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
  },
  drawer: {
    width: 200,
    flexShrink: 0,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-start',
  },
  drawerPaper: {
    width: 200,
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const SearchAppBar = ({user, setUser}) => {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = useState(false);
  const [openLogged, setOpenLogged] = useState(false);
  const history = useHistory()

  const handleCloseLogged = (event, reason) => {
    if (reason === 'clickaway') {
        return;
    }
    setOpenLogged(false);
    };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };  

  const handleLogout = () => {
    setUser(null)
    storage.logoutUser()
    
    history.push('/')
    setOpenLogged(true)
    
  }

  if (user){
    return (
      <div className={classes.root}>
        <AppBar position="static" style={{ background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)" }}>
          <Toolbar>
          <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography className={classes.title} variant="h6" noWrap>
              Book-Finder
            </Typography>
            <div className={classes.search}>
              <Button onClick = {handleLogout}>
                Log out
              </Button>
            </div>
          </Toolbar>
        </AppBar>
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={handleDrawerClose}>
              {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
            </IconButton>
          </div>
          <List >
                <Link to = "/" style={{ textDecoration: 'none', color: "black"}}>
                  <ListItem button>
                    <ListItemText primary= "Home" />
                  </ListItem>
                </Link>
                <Link to = {`/users/${user.id}`} style={{ textDecoration: 'none', color: "black"}}>
                  <ListItem button>
                    <ListItemText primary= "My booklist" />
                  </ListItem>
                </Link>
                <ListItem button onClick = {handleLogout}>
                  <ListItemText primary = "Logout" />
                </ListItem>
          </List>
        </Drawer>
            <Snackbar open={openLogged} autoHideDuration={3000} onClose={handleCloseLogged}>
                <Alert onClose={handleCloseLogged} severity="success">
                Logged out
                </Alert>
            </Snackbar>
      </div>
    )
  };

    return (
      <div className={classes.root}>
        <AppBar position="static" style={{ background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)" }}>
          <Toolbar>
          <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography className={classes.title} variant="h6" noWrap>
              Book-Finder
            </Typography>
            <div className={classes.search}>
              <Link to = {`/login`} style={{ textDecoration: 'none', color: "white" }} >
                  <Button>
                    Login
                  </Button>
                </Link>
            </div>
          </Toolbar>
        </AppBar>
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={handleDrawerClose}>
              {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
            </IconButton>
          </div>
          <List >
                <Link to = "/" style={{ textDecoration: 'none', color: "black"}}>
                  <ListItem button>
                    <ListItemText primary= "Home" />
                  </ListItem>
                </Link>
                <Link to = {`/login`} style={{ textDecoration: 'none', color: "black"}}>
                  <ListItem button>
                    <ListItemText primary= "Login" />
                  </ListItem>
                </Link>
                <Link to = {`/signup`} style={{ textDecoration: 'none', color: "black"}}>
                  <ListItem button>
                    <ListItemText primary= "Create an account" />
                  </ListItem>
                </Link>
          </List>
        </Drawer>
        <Snackbar open={openLogged} autoHideDuration={6000} onClose={handleCloseLogged}>
                <Alert onClose={handleCloseLogged} severity="success">
                Logged out
                </Alert>
            </Snackbar>
      </div>
    );
  



}

export default SearchAppBar