import { useEffect, useState } from 'react';

import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import SearchService from '../services/books'


  const useStyles = makeStyles((theme) => ({
      button: {
        margin: theme.spacing(1),
        justifyContent: 'center'
      },
      root: {
          width: '100%',
        },
        container: {
          maxHeight: 440,
        },
    }));
  
    const columns = [
      { id: 'BranchName', label: 'Branch', minWidth: 170 },
      { id: 'LocationDesc', label: 'Section', minWidth: 100 },
      { id: 'CallNumber', label: 'Call number', minWidth: 100 },
      { id: 'StatusDesc', label: 'Status', minWidth: 100 },
      { id: 'StatusDate', label: 'Status Date', minWidth: 100 },
      { id: 'DueDate', label: 'Due Date', minWidth: 100 },
  
    ];

const SearchDialog = ({available, handleClose, open}) => {
    const classes = useStyles();
  
    return (
        <Dialog
        open={open}
        onClose={handleClose}
        fullWidth = {true}
        maxWidth = "md"
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title">Availability</DialogTitle>
        <DialogContent>
          <DialogContentText
            id="scroll-dialog-description"
            tabIndex={-1}
          >
              <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {available.map((row) => {
              return (
                <TableRow hover role="checkbox" tabIndex={-1} key={row.BranchID}>
                  {columns.map((column) => {
                    const value = row[column.id];
                    return (
                      <TableCell key={column.id} align={column.align}>
                        {column.format && typeof value === 'number' ? column.format(value) : value}
                      </TableCell>
                    );
                  })}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>    
    )
}


const SearchAvailable = ({bid}) => {
    const [available, setAvailable] = useState([])
    const [open, setOpen] = useState(false);
  
    const handleClickOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };
  
    useEffect(() => {
        async function getTitleDetails(){
          const availableResult = await SearchService.searchAvail(bid)
          setAvailable(availableResult)
        }
        getTitleDetails()
    }, [bid])
  
  return(
    <Box textAlign = "center">
        <Tooltip title = "Search availability">
            <IconButton onClick = {handleClickOpen}>
                <SearchIcon/>
            </IconButton>
        </Tooltip>
    <SearchDialog open = {open} handleClose = {handleClose} available = {available} />
    </Box>
  )
    }
    
    export default SearchAvailable