
import { useState } from 'react';

import Box from '@material-ui/core/Box';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';

import UserService from '../services/users'



function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}


const DeleteButton = ({ user, row}) => {
  const [openNoti, setOpenNoti] = useState(false);
  const [snack, setSnack] = useState("Loading...")
  const [severity, setSeverity] = useState("success")

  const handleNotiClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenNoti(false);
  };


  const handleDelete = async () => {
    console.log(row)
    if (window.confirm(`Are you sure you want to delete ${row.TitleName.split('/')[0]}?`)){
      try{
        await UserService.deleteBook({bid: row.BID, id: user.id})
        setSeverity("success")
        setSnack("Book deleted")
        setOpenNoti(true)
        window.location.reload(false)
      } catch(exception){
        setSnack(exception.message)
        setSeverity("error")
        setOpenNoti(true)
      }
    }
  }

  return(
    <Box textAlign = "center">
        <Tooltip title = "Delete from your readlist">
            <IconButton aria-label="delete" onClick = {handleDelete}>
                <DeleteIcon />
            </IconButton>
        </Tooltip>
    <Snackbar open={openNoti} autoHideDuration={2000} onClose={handleNotiClose}>
          <Alert onClose={handleNotiClose} severity= {severity}>
            {snack}
          </Alert>
        </Snackbar>     
    </Box>

  )
  }
  
  export default DeleteButton