
import { useState } from 'react';

import Box from '@material-ui/core/Box';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';

import UserService from '../services/users'



function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}


const AddButton = ({ user, row}) => {
  const [openNoti, setOpenNoti] = useState(false);
  const [snack, setSnack] = useState("Loading...")
  const [severity, setSeverity] = useState("success")

  const handleNotiClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenNoti(false);
  };


  const handleAdd = async () => {
    if (user === undefined){
      setSeverity("error")
      setSnack("Error: Unable to add book if you are not logged in")
      setOpenNoti(true)
    }else{
      try{
        await UserService.addBook({book: row, id: user.id})
        setSeverity("success")
        setSnack("Book added to readlist")
        setOpenNoti(true)
      } catch(exception){
        setSnack(exception.message)
        setSeverity("error")
        setOpenNoti(true)
      }
    }

  }

  return(
    <Box textAlign = "center">
        <Tooltip title = "Add to your readlist">
            <IconButton aria-label="add" onClick = {handleAdd}>
                <AddCircleIcon />
            </IconButton>
        </Tooltip>
    <Snackbar open={openNoti} autoHideDuration={2000} onClose={handleNotiClose}>
          <Alert onClose={handleNotiClose} severity= {severity}>
            {snack}
          </Alert>
        </Snackbar>     
    </Box>
  )
  }
  
  export default AddButton