import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import IconButton from '@material-ui/core/IconButton';
import SearchAccordion from './SearchAccordion'
import Tooltip from '@material-ui/core/Tooltip';

import SearchAvailable from './SearchAvailable'
import DeleteButton from './DeleteButton'

const columns = [
  { id: 'TitleName', label: 'Title', minWidth: 170 },
  { id: 'Author', label: 'Author', minWidth: 100 },
  { id: 'PublishYear', label: 'Publish Year', minWidth: 100 },
  { id: 'MediaDesc', label: 'Media Description', minWidth: 100 },

];

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
  },
});

const useRowStyles = makeStyles({
  root: {
    '& > *': {
      borderBottom: 'unset',
    },
  },
});


function Row({row, user}){
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();



  return(
    <React.Fragment>
      <TableRow className = {classes.root}>
        {columns.map((column) => {
          console.log(row[column.id])
          var value = row[column.id];
          if (value === undefined){
            value = "-"
          }
          return (
            <TableCell key={column.id} align={column.align}>
              {column.format && typeof value === 'number' ? column.format(value) : value.split('/')[0]}
            </TableCell>);
          })}
        <TableCell>
          <Tooltip title = "Get more details">
            <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </Tooltip>
        </TableCell>
        <TableCell>
          <DeleteButton row = {row} user = {user} />
        </TableCell>
        <TableCell>
          <SearchAvailable bid = {row.BID} />
        </TableCell>
          </TableRow>
          <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Box margin={1}>
                <SearchAccordion key = {row.BID} bid = {row.BID} user = {user} row = {row} />
                </Box>
              </Collapse>
              </TableCell>
          </TableRow>
    </React.Fragment>
  )
}

export default function StickyHeadTable({rows, user}) {

  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };
  console.log(rows)

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table >
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
              return (
                     <Row key = {row.BID} row = {row} user = {user} /> 
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 50]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </Paper>
  );
}
