import { useState } from 'react';
import { Formik, Form, } from "formik";

import { Link, useHistory} from "react-router-dom";

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import userService from '../services/users'
import loginService from '../services/login'
import storage from '../utils/storage'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        '& > * + *': {
          marginTop: theme.spacing(2),
        },
      },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

const SignUpPage = ({setUser}) => {
  const classes = useStyles();
  const [openSuccess, setOpenSuccess] = useState(false);
  const [openFail, setOpenFail] = useState(false);
  const history = useHistory();

  const handleCloseSuccess = (event, reason) => {
      if (reason === 'clickaway') {
        return;
      }
      setOpenSuccess(false);
    };

  const handleCloseFail = (event, reason) => {
  if (reason === 'clickaway') {
      return;
  }
  setOpenFail(false);
  };
  const handleSignUp = async (values) =>{
    console.log(values)
    try {
        await userService.addUser({username: values.username, password: values.password, name: values.name})
        const user = await loginService.login({
            username: values.username, password: values.password
          })
        setUser(user)
        storage.saveUser(user)
        setOpenSuccess(true)
        history.push('/')
      } catch(exception) {
        console.log(exception)
        setOpenFail(true)
      }
  }
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <Formik
              initialValues={{ username: "", password: "" , name:""}}
              onSubmit={(values) => 
                handleSignUp(values)
              }>
              {(formikProps) => (
        <Form className={classes.form} noValidate>
          <Grid container spacing={2}>
          <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="name"
                label="Name"
                name="name"
                autoComplete="name"
                onChange={formikProps.handleChange}
                value={formikProps.values.name}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="username"
                label="Username"
                name="username"
                autoComplete="username"
                onChange={formikProps.handleChange}
                value={formikProps.values.username}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                onChange={formikProps.handleChange}
                value={formikProps.values.password}
              />
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign Up
          </Button>
        </Form>)}
        </Formik>
        <Grid container justify="flex-start">
            <Grid item>
              <Link to = "/login" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        <Snackbar open={openSuccess} autoHideDuration={3000} onClose={handleCloseSuccess}>
            <Alert onClose={handleCloseSuccess} severity="success">
            User created!
            </Alert>
        </Snackbar>
        <Snackbar open={openFail} autoHideDuration={3000} onClose={handleCloseFail}>
            <Alert onClose={handleCloseFail} severity="error">
            Username has been taken. Choose a new one
            </Alert>
        </Snackbar>
      </div>
    </Container>
  );
}

export default SignUpPage;