import { useEffect, useState } from 'react';

import Typography from '@material-ui/core/Typography';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

import SearchService from '../services/books'


function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const SearchAccordion = ({bid, user, row}) => {
  const [result, setResult ] = useState([])
  const [openNoti, setOpenNoti] = useState(false);
  const [snack, setSnack] = useState("Loading...")


  const handleNotiClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenNoti(false);
  };


  useEffect(() => {
      async function getTitleDetails(){
        setOpenNoti(true)
        const TitleResult = await SearchService.searchTitle(bid)
        setResult(TitleResult)
        setOpenNoti(false)
      }
      getTitleDetails()
  }, [bid])


    if (result.Summary === ""){
        return (
            <div>
                <div>
                    No additonal details
                </div>
                  <Snackbar open={openNoti} onClose={handleNotiClose}>
                    <Alert onClose={handleNotiClose} severity="info">
                      {snack}
                    </Alert>
                  </Snackbar>                 
            </div>
        )
    }
    return (
        <div >
            <Typography variant="h6" gutterBottom component="div">
                  Summary
            </Typography>
        <div> 
            {result.Summary}  
            <Snackbar open={openNoti} onClose={handleNotiClose}>
                    <Alert onClose={handleNotiClose} severity="info">
                      {snack}
                    </Alert>
                  </Snackbar>            
        </div>        
        </div>

    )
  }
  
  export default SearchAccordion