import {React, useState } from 'react';
import { useHistory } from 'react-router';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import Container from '@material-ui/core/Container';
import SearchIcon from '@material-ui/icons/Search';
import { Formik, Form, } from "formik";
import InputAdornment from '@material-ui/core/InputAdornment';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { spacing } from '@material-ui/system';
import Typography from '@material-ui/core/Typography';

import SearchService from '../services/books'
import SearchPlatformTable from './SearchPlatformTable'

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const SearchPlatform = ({user}) => {
  const [result, setResult ] = useState([])
  const [open, setOpen] = useState(false);
  const history = useHistory()

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const searchFunction = async (query) =>{
    history.push(`/search/${query}`)
    setOpen(true)
    const result = await SearchService.searchBook(query)
    setOpen(false)
    setResult(result)
  }

    return (
        <Container maxWidth="md">
             <div>
                <Formik
                initialValues={{ query: "" }}
                onSubmit={(values) => 
                  searchFunction(values.query)
                }>
                {(formikProps) => (
                    <Form>
                        <TextField
                        fullWidth
                        variant="outlined"
                        colour = "primary"
                        placeholder="Search…"
                        name = "query"
                        InputProps={{ endAdornment: (
                            <InputAdornment position="start">
                                <IconButton variant="contained" type="submit">
                                    <SearchIcon />
                                </IconButton>
                            </InputAdornment>
                           ) }}
                        onChange={formikProps.handleChange}
                        value={formikProps.values.query}
                        />
                    </Form>)}
                </Formik>
            </div>
            <Snackbar open={open} onClose={handleClose}>
              <Alert onClose={handleClose} severity="info">
                Loading...
              </Alert>
            </Snackbar>                 
      </Container>
    )
  }
  
  export default SearchPlatform